<template>
  <ItemGrid :xhrs="xhrs" :new-item="newItem" :heading="heading" i18n-key="revision" id-prop="revisionId">
    <template slot="headline" slot-scope="{ item }">
      {{ formatCarNumber(item.car.name) }}
    </template>
    <template slot-scope="{ item }">
      <RevisionDL :item="item" />
    </template>
    <template slot="edit" slot-scope="{ item, itemCb, show, showCb }">
      <RevisionDialog
        ref="dialog"
        :value="item" :show="show"
        @input="itemCb" @show="showCb"
      />
    </template>
  </ItemGrid>
</template>

<script>
import { getRevisions, postRevision, putRevision, deleteRevision, getCarRevisions, getCar } from '@/axios'

import ItemGrid from '@/components/ItemGrid'
import RevisionDL from '@/components/itemDL/Revision'
import RevisionDialog from '@/components/RevisionDialog'
import { formatCarNumber } from '@/common'

export default {
  components: { ItemGrid, RevisionDL, RevisionDialog },
  data: () => ({
    formatCarNumber,
    getCarRevisions,
    heading: null,
    newItem: {}
  }),
  computed: {
    xhrs () {
      const carId = this.$route.query.carId
      if (carId != null) {
        return {
          searchItems: null,
          getItems: getCarRevisions.bind(null, carId * 1),
          postItem: postRevision,
          putItem: putRevision,
          deleteItem: deleteRevision
        }
      } else {
        return {
          searchItems: null,
          getItems: getRevisions,
          postItem: postRevision,
          putItem: putRevision,
          deleteItem: deleteRevision
        }
      }
    }
  },
  watch: {
    xhrs () {
      this.updateHeading()
    }
  },
  mounted () {
    this.updateHeading()
  },
  methods: {
    async updateHeading () {
      const carId = this.$route.query.carId
      if (carId != null) {
        const car = await getCar(carId)
        this.heading = formatCarNumber(car.name)
      } else {
        this.heading = null
      }
    }
  }
}
</script>
