<template>
  <ItemGrid :xhrs="xhrs" :new-item="newItem" :heading="heading" i18n-key="lease" id-prop="leaseId">
    <template slot="headline" slot-scope="{ item }">
      {{ formatCarNumber(item.car.name) }}
    </template>
    <template slot-scope="{ item }">
      <LeaseDL :item="item" />
    </template>
    <template slot="edit" slot-scope="{ item, itemCb, show, showCb }">
      <LeaseDialog
        ref="dialog"
        :value="item" :show="show"
        @input="itemCb" @show="showCb"
      />
    </template>
  </ItemGrid>
</template>

<script>
import { getLeases, postLease, putLease, deleteLease, getCar, getSubject } from '@/axios'

import ItemGrid from '@/components/ItemGrid'
import LeaseDL from '@/components/itemDL/Lease'
import LeaseDialog from '@/components/LeaseDialog'
import { formatCarNumber } from '@/common'

export default {
  components: { ItemGrid, LeaseDL, LeaseDialog },
  data: () => ({
    formatCarNumber,
    heading: null,
    newItem: {},
    xhrs: {
      searchItems: null,
      getItems: getLeases,
      postItem: postLease,
      putItem: putLease,
      deleteItem: deleteLease
    }
  }),
  watch: {
    xhrs () {
      this.updateHeading()
    }
  },
  mounted () {
    this.updateHeading()
  },
  methods: {
    async updateHeading () {
      const carId = this.$route.query.carId
      if (carId != null) {
        const car = await getCar(carId)
        this.heading = formatCarNumber(car.name)
        return
      }

      const subjectId = this.$route.query.subjectId
      if (subjectId != null) {
        const subject = await getSubject(subjectId)
        this.heading = subject.name
        return
      }

      this.heading = null
    }
  }
}
</script>
